import cx from 'classnames';
import FundiinButton from 'components/Common/FundiinButton';
import { ReactElement, useState } from 'react';
import { useTranslation } from 'react-i18next';

export type PropsTabSection = {
  title: ReactElement;
  content: ReactElement;
};

type PropsTab1Section = {
  title: string;
  tabs: PropsTabSection[];
} & React.HTMLAttributes<HTMLDivElement>;

function Tab1({ title, className, tabs = [] }: PropsTab1Section) {
  const { t } = useTranslation();
  const [activeTab, setActiveTab] = useState(0);

  const handleActiveTab = (id: number) => {
    setActiveTab(id);
  };

  return (
    <div className={cx(['tab1', className])}>
      <div className="container">
        <h3 className="tab1__title color-grey-1000">{t(title)}</h3>
        <div className="tab1__contronler">
          {tabs.map((ta, i) => (
            <FundiinButton
              key={i}
              className={cx([activeTab === i ? 'active' : ''])}
              onClick={() => handleActiveTab(i)}
            >
              {ta.title}
            </FundiinButton>
          ))}
        </div>
        <div className="tab1__content">
          {tabs.map((ta, i) =>
            activeTab === i ? (
              <div key={i} className="tab1__item">
                {ta.content}
              </div>
            ) : null
          )}
        </div>
      </div>
    </div>
  );
}

export default Tab1;
